import { Component } from "react";
import Swiper, { Navigation, Autoplay } from 'swiper';
import 'swiper/swiper-bundle.css';
import '../css/main.css';
import $ from 'jquery';

Swiper.use([Navigation, Autoplay]);

function openRequest() {
    $('.mobile').removeClass('off');
}

class Main extends Component {
    componentDidMount() {
        var  ww = $(window).width();
        var swiper = undefined;
        
        function initSwiper() {
            if( ww > 991 && swiper === undefined)  {
                swiper = new Swiper('#swiper', {
                    loop: true,
                    slideToClickedSlide: true,
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    },
                    autoplay: {
                        delay: 3000,
                    },
                    breakpoints: {
                        992: {
                            slidesPerView: 2,
                            spaceBetween: 25
                        },
                        1455: {
                            slidesPerView: 3,
                            spaceBetween: 30
                        }
                    }
                })
            } else if( ww < 992 && swiper !== undefined) {
                swiper.destroy();
                swiper = undefined;
            }
        }

        function responsive() {
            if( ww > 1399 ) {
                $('#request').removeClass('tablet mobile off');
                $('#mobile').addClass('off');
                $('#request h2').addClass('col-2').removeClass('col-11 responsive');
                $('#request form').addClass('col-10').removeClass('col-12');
                $('#request form > div > div').removeClass('responsive');
                $('#request form > div > input').removeClass('responsive');
                $('#request form span').removeClass('responsive');
            } else if( ww > 992) {
                $('#request').addClass('tablet').removeClass('mobile off');
                $('#mobile').addClass('off');
                $('#mobile').addClass('off');
                $('#request h2').addClass('responsive').removeClass('col-2 col-11');
                $('#request form').addClass('col-12').removeClass('col-10');
                $('#request form > div').addClass('col-9').removeClass('col-11');
                $('#request form > div > div').addClass('responsive');
                $('#request form > div > input').addClass('responsive');
                $('#request form span').addClass('responsive');
            } else {
                $('#request').addClass('mobile off').removeClass('tablet');
                $('#mobile').removeClass('off');
                $('#request h2').addClass('col-11').removeClass('col-2 responsive')
                $('#request form').addClass('col-12').removeClass('col-10');
                $('#request form > div').addClass('col-11');
                $('#request form > div > div').addClass('responsive');
                $('#request form > div > input').addClass('responsive');
                $('#request form span').addClass('responsive');
            }
        }

        

        initSwiper();
        responsive();
        
        $(window).resize(function() {
            ww = $(window).width();
            initSwiper();
            responsive();
        })

        $('#openRequest').click(function() {
            $('#request').removeClass('off');
        });
        
        $('.mobile .xi-close').click(function() {
            $('#request').addClass('off');
        })

        
        $(document).ready(function() {
            

            $('.swiper-button-prev').css({'background-image' : 'url(src/img/icons/left.png)'})
            $('.swiper-button-next').css({'background-image' : 'url(src/img/icons/right.png)'})

            $('.swiper-button-prev').hover(
                function() {
                $(this).css({'background-image' : 'url(src/img/icons/left-hover.png)'})
                },
                function() {
                    $(this).css({'background-image': 'url(src/img/icons/left.png)'})
                }
            )

            $('.swiper-button-next').hover(
                function() {
                $(this).css({'background-image' : 'url(src/img/icons/right-hover.png)'})
                },
                function() {
                    $(this).css({'background-image': 'url(src/img/icons/right.png)'})
                }
            )
        })
    }
    render() {
        return(
            <div id='main'>
                <section id='mainBanner'>
                    <div className='container'>
                        <div className='row'>
                            <div className='textArea'>
                                <i>BK partners</i>
                                <h2>담보대출도 신용대출도<br/> 믿을 수 있는 신뢰할수 있는 <br/><b>BK PARTNERS와 함께</b></h2>
                            </div>
                            <img src='src/img/main_banner.svg'></img>
                        </div>
                    </div>
                </section>
                <section id='itemList'>
                    <h2>BK PARTNERS 맞춤대출 LIST</h2>
                    <p>비케이 파트너스의 여러종류 대출을 소개합니다.</p>
                    <div id='listSlide'>
                        <div id='swiper' className='container'>
                            <div className='swiper-wrapper'>
                                <div className='swiper-slide'>
                                    <i>당장이라도 이사하고 싶은 마음이 굴뚝같을땐</i>
                                    <h3>전월세보증금대출</h3>
                                    <div className='row'>
                                        <img src='src/img/icons/icon01.png'></img>
                                        <p className='listNum'>01</p>
                                    </div>
                                </div>
                                <div className='swiper-slide'>
                                    <i>만 20세 이상 성인들을 위한 맞춤 대출 서비스</i>
                                    <h3>개인신용대출</h3>
                                    <div className='row'>
                                        <img src='src/img/icons/icon02.png'></img>
                                        <p className='listNum'>02</p>
                                    </div>
                                </div>
                                <div className='swiper-slide'>
                                        <i>소유한 부동산으로 빠른 자금 융통이 가능한</i>
                                        <h3>부동산담보대출</h3>
                                        <div className='row'>
                                            <img src='src/img/icons/icon03.png'></img>
                                            <p className='listNum'>03</p>
                                        </div>
                                </div>
                                <div className='swiper-slide'>
                                        <i>타던 차 그대로 자동차 담보대출</i>
                                        <h3>자동차대출</h3>
                                        <div className='row'>
                                            <img src='src/img/icons/icon04.png'></img>
                                            <p className='listNum'>04</p>
                                        </div>
                                </div>
                                <div className='swiper-slide'>
                                        <i>신속하고 간편한 진행절차</i>
                                        <h3>회생 / 회복 / 파산자 대출</h3>
                                        <div className='row'>
                                            <img src='src/img/icons/icon05.png'></img>
                                            <p className='listNum'>05</p>
                                        </div>
                                </div>                        
                            </div>
                        </div>
                        <div className='swiper-button-prev' style={{backgroundImage: "url('src/img/icons/left.png')"}}></div>
                        <div className='swiper-button-next' style={{backgroundImage: "url('src/img/icons/right.png')"}}></div>
                    </div>
                </section>
                <section id='request'>
                    <div className='container'>
                        <div className="row">
                            <h2 className='col-2'>빠르고 간편한 친절 상담신청<span className='xi-close'></span></h2>
                            <form action="" method="post" className='row col-10'>
                                <div className="row col-9">
                                    <input type="text" name="name" id="name" className='col-2'  placeholder='성함'/>
                                    <input type="text" name="name" id="phone" className='col-3' placeholder='연락처'/>
                                    <input type="text" name="name" id="price"  className='col-3' placeholder='신청금액'/>
                                    <div className="col-3">
                                        <input type="checkbox" name="agree" id="agree" />
                                        <label htmlFor="agree">개인정보 취급방침동의</label>
                                    </div>
                                    <span>고객님의 정보는 절대 유출되지 않으며, 한도조회 및 단순상담은 신용정보에 영향을 주지 않습니다.</span>
                                </div>
                                <input type="submit" value="상담신청" className='col-3' />
                            </form>
                        </div>
                    </div>
                </section>
                <div id="mobile" className="off">
                    <div className="container">
                        <input type="button" value="상담신청하기" id='openRequest' />
                    </div>
                </div>
            </div>
        );
    }
}

export default Main;